*{
  box-sizing: border-box;
}

body{
  margin: 0;
  background-color: #189ab4

  ;
}

.container {
  margin: 1rem;
  text-align: center;
  border: 8px solid black;
  background-color: #75e6da;
}

.Navbar{
  background-color: #05445e;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding: 0 1rem;
  min-height: 80px;
}

.site-title{
  font-size: 3rem;
}

.Navbar li{
  font-size: 2rem;
}

.Navbar ul{
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
}

.Navbar a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25 rem;
}

.Navbar li:hover{
  background-color: #777;
}
